.list{
    display: flex;
    width:800px;
    justify-content: space-between;
    margin:10px;
    margin-left:20%;
    background-color: #5f694f;
}
.list button{
    font-size: 1.7em;
    font-weight: bold;
    border:rebeccapurple;
    outline:none;
    cursor: pointer;
    margin:10px;
    background-color: #f1af71;
    justify-content: space-around;
    font-size: 1.2em;
    border-radius: 10%;
}
.addbtn{
    font-size: 1.7em;
    font-weight: bold;
    border:rebeccapurple;
    outline:none;
    cursor: pointer;
    margin:10px;
    background-color: #f1af71;
    justify-content: space-around;
    font-size: 1.2em;
    border-radius: 10%;
}
.details{
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .details td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .details tr:nth-child(even){background-color: #f2f2f2;}
  
  .details tr:hover {background-color: #ddd;}
  
  .details th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
  }
  .addtodo input{
    font-weight: 500;
  }